import { Injectable } from '@angular/core';
import { AngularFirestoreCollection, AngularFirestore } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { OrderDetail } from 'src/Table/table';
import { AppError } from '../common/app-error';
import { BadInput } from '../common/bad-input';
import { NotFoundError } from '../common/not-found-error';

@Injectable({
  providedIn: 'root'
})
export class OrderDetailService {
  private orderDetailCollectionList: AngularFirestoreCollection<any>;
  private orderDetailList: Observable<any[]>;
  constructor(private db: AngularFirestore) {
    this.orderDetailCollectionList = db.collection<OrderDetail>('OrderDetail');
  }
  create(orderObj) { 
    return this.orderDetailCollectionList.add(orderObj)
  }
  getAllOrderDetail() {
    this.orderDetailList = this.orderDetailCollectionList.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
    return this.orderDetailList;
  }
  getOrderDetail(id) {
    return this.orderDetailCollectionList.doc<any>(id).valueChanges();
  }
  updateOrderDetail(orderObj: OrderDetail, id: string) {
    return this.orderDetailCollectionList.doc(id).update(orderObj)
    .catch(this.handleError);
  }
  removeOrderDetail(id) {
    return this.orderDetailCollectionList.doc(id).delete()
    .catch(this.handleError);
  }
  private handleError(error: Response) {
    if (error.status === 400)
      return Observable.throw(new BadInput(error.json()));
    if (error.status === 404)
      return Observable.throw(new NotFoundError());
     return Observable.throw(new AppError(error));
  }
}
