import { Injectable } from '@angular/core';
import { AngularFirestoreCollection, AngularFirestore } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { Food} from 'src/Table/table';
import { AppError } from '../common/app-error';
import { BadInput } from '../common/bad-input';
import { NotFoundError } from '../common/not-found-error';

@Injectable({
  providedIn: 'root'
})
export class FoodService {
  private foodCollectionList: AngularFirestoreCollection<any>;
  private foodList: Observable<any[]>;
  constructor(private db: AngularFirestore) {
    this.foodCollectionList = db.collection<Food>('Food');
  }
  create(foodObj) { 
    return this.foodCollectionList.add(foodObj)
  }
  getAllFood() {
    this.foodList = this.foodCollectionList.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
    return this.foodList;
  }
  getFood(id) {
    return this.foodCollectionList.doc<any>(id).valueChanges();
  }
  updateFood(foodObj: Food, id: string) {
    return this.foodCollectionList.doc(id).update(foodObj)
    .catch(this.handleError);
  }
  removeFood(id) {
    return this.foodCollectionList.doc(id).delete()
    .catch(this.handleError);
  }
  private handleError(error: Response) {
    if (error.status === 400)
      return Observable.throw(new BadInput(error.json()));
  
    if (error.status === 404)
      return Observable.throw(new NotFoundError());
    
    return Observable.throw(new AppError(error));
  }
}
